body{
  background-color: #DFD3C3;
}

.rootDiv{
  display: grid; 
  height:100vh;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr; 
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr; 
  gap: 0px 0px; 
  grid-template-areas: 
    ". . . . . . ."
    ". . . . . . ."
    ". . . . . . ."
    ". . timer . . . ."
    ". . . . . . .";
}

.days{
  grid-area: days;
}
.hours{
  grid-area: hours;
}
.minutes{
  grid-area: minutes;
}
.seconds{
  grid-area: seconds;
}
.timer{
  grid-area: timer;
}
.timer{
  width: 30vw;
  height: 10vw;
  border: #C7B198 solid 5px;
  background-color: #F0ECE3;
  display: grid; 
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr; 
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr; 
  gap: 0px 0px; 
  grid-template-areas: 
    ". . . . . . . . ."
    ". . . . . . . . ."
    ". days daysSep hours hoursSep minutes minutesSep seconds ."
    ". . . . . . . . ."
    ". . . . . . . . .";
  transition: 2s ease;
}

.daysSep{grid-area:daysSep} .hoursSep{grid-area:hoursSep} .minutesSep{grid-area:minutesSep}

.days,.hours,.minutes,.seconds{
  color:#C7B198;
  transition: 2s ease
}

.daysSep, .hoursSep, .minutesSep{
  color: #596E79;
  transition: 2s ease;

}

.speakermanCopy{
  font-size: 1vw;
  color: white;
  text-align: end;
  position: absolute;
  top:80%;
}